import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import retrieve from 'utils/retrieve';
import DataTable from 'components/DataTable';
import VisitButton from 'components/VisitButton';
import { sortBy } from 'utils/sorting';
import CellImage from 'components/CellImage';
import { getItemImageUrl, getTextLinkHtml } from 'utils/retrieve';

export default function PCItemsTable({ prefilters = {} }) {
  const [data, setData] = useState([]);

  useEffect(() => {
    (async () => {
      const records = await retrieve('自付差額品項');
      setData(records.sort(sortBy('中文')).sort(sortBy('分類')).sort(sortBy('類別')));
    })();
  }, []);

  const columns = [
    {
      name: '代碼',
      label: '圖片',
      options: {
        filter: false,
        sort: false,
        customBodyRender: (value) => <CellImage value={getItemImageUrl(value)} />,
      },
    },
    {
      name: '類別',
      label: '類別',
      options: {
        filterList: prefilters['類別'] || [],
        filterOptions: {
          fullWidth: true,
        },
      },
    },
    {
      name: '分類',
      label: '分類',
      options: {
        filterList: prefilters['分類'] || [],
        filterOptions: {
          fullWidth: true,
        },
      },
    },
    {
      name: '代碼',
      label: '代碼',
      options: {
        filter: false,
      },
    },
    {
      name: '中文',
      label: '中文',
      options: {
        filter: false,
      },
    },
    {
      name: '英文',
      label: '英文',
      options: {
        filter: false,
        display: false,
      },
    },
    {
      name: '許可證字號',
      label: '許可證字號',
      options: {
        filter: false,
        display: false,
      },
    },
    {
      name: '健保給付點數',
      label: '健保給付點數',
      options: {
        filter: false,
      },
    },
    {
      name: '最低自付差額',
      label: '最低自付差額',
      options: {
        filter: false,
      },
    },
    {
      name: '最高自付差額',
      label: '最高自付差額',
      options: {
        filter: false,
      },
    },
    {
      name: '醫療機構數',
      label: '醫療機構數',
      options: {
        filter: false,
      },
    },
    {
      name: '代碼',
      label: ' ',
      options: {
        filter: false,
        sort: false,
        customBodyRender: (value) => (<VisitButton url={`/pcItem/${value}`} title="查看" target="_blank" />),
      },
    },
  ];

  return (
    <DataTable
      title="自付差額醫療特材清單"
      description={getTextLinkHtml('資料出處：健保署 民眾自付差額品項收費情形', 'https://data.nhi.gov.tw/Datasets/DatasetDetail.aspx?id=663&Mid=A110734')}
      data={data}
      columns={columns}
    />
  );
}

PCItemsTable.propTypes = {
  prefilters: PropTypes.object,
};
